<template>
<div class="scrollBox">
  <header-nav v-if="$utils.showNavBar()"></header-nav>
  <div id='movieComment' :class="{hasHeader: $utils.showNavBar()}">
    <div class="scroll-touch" infinite-scroll-distance="50" v-infinite-scroll="loadMore">
      <!-- 有影评 -->
      <div class="list" v-if="list.length > 0">
        <div class="comment-item" v-for="(item, index) in list" @click="toDetail(index)" v-if="item.movieinfo" :key="index">
          <div class="comment-type">
            <span>点评{{item.movieinfo.type_id_1}}</span>
          </div>
          <div class="comment-grade">
             <div class="grade">
              <div class="grade-icon">
                <div class="grade_progress" :style="{width: `${item.score * 10}%`}"></div>
              </div>
              <div class="acturally mR10">{{item.score}}</div>
              <div class="acturally">
                <span v-if="item.review_score * 1 < 2">超烂</span>
                <span v-else-if="item.review_score * 1 <= 4">较差</span>
                <span v-else-if="item.review_score * 1 <= 6">一般般</span>
                <span v-else-if="item.review_score * 1 <= 8">推荐</span>
                <span v-else-if="item.review_score * 1 <= 10">力荐</span>
              </div>
            </div>
          </div>
          <div class="content">{{item.content}}</div>
          <div class="list-item">
            <div class="left-cover item-cover">
              <img v-if="item.movieinfo.vod_pic" onerror="this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/default_image.png'" :src="$utils.getPlayUrl(item.movieinfo.vod_pic)" alt="">
              <img src="" v-else alt="">
            </div>
            <div class="rightContent">
              <div class="title">{{item.movieinfo.vod_name}}</div>
              <div class="tip">{{item.movieinfo.vod_class}}</div>
              <div class="tip">{{item.movieinfo.vod_year > 1900 ? item.movieinfo.vod_year : '未知'}}</div>
              <div class="tip grades">
                <div class="juejinbao">掘金宝{{item.score ? item.score : 0}}分</div>
                <div class="douban">豆瓣{{item.movieinfo.vod_douban_score}}分</div>
              </div>
            </div>
          </div>
          <div class="update-time flex-row jusB">
            <div class="update">{{$utils.timeFormat(item.comment_time * 1000)}}</div>
            <div class="digg" @click.stop="setDigg(index)" :class="{active: item.is_fabulous === 1}">
              <div class="digg-icon">
                <img v-if="item.is_fabulous === 1" src="../../../assets/img/index/digg_active.png" alt="">
                <img v-else src="../../../assets/img/index/digg.png" alt="">
              </div>
              <div class="text">{{item.fabulous}}</div>
            </div>
          </div>
        </div>
        <div class="list-no-data" v-if="loading > 0">{{loading === 1 ? '加载中...' : '没有更多数据了！'}}</div>
      </div>
      <div class="no-list" v-if="list.length === 0 && loading === 2">
        <div class="image iconSize">
          <img src="../../../assets/img/movie/comment-icon.png" alt="无影评">
        </div>
        <div class="text color666 f32 lh46 text-center">暂无相关影评～</div>
      </div>
    </div>
  </div>
</div>
  
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
export default {
  components: {
    HeaderNav
  },
  data () {
    return {
      list: [],
      loading: 0,
      page: 1
    }
  },
  methods: {
    loadMore () {
      if (this.loading === 1 || this.loading === 2) {
        return
      }
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.timer = setTimeout(() => {
        this.loading = 1
        this.getList(res => {
          this.list = [...this.list, ...res.data.data]
        })
      }, 300)
    },
    // 点赞
    setDigg (index) {
      if (this.list[index].is_fabulous === 0) {
        this.$api.post('MOVIE_COMMENT_FABULOUS', {vod_id: this.list[index].vod_id, reviewid: this.list[index].reviewid}, res => {
          let obj = this.list[index]
          obj.is_fabulous = 1
          obj.fabulous = 1
          this.$set(this.list, index, obj)
        })
      }
    },
    toDetail (index) {
      this.$router.push({path: `/film/detail/${this.list[index].vod_id}`})
    },
    getList (cb) {
      let data = {
        page: this.page
      }
      this.$api.get('MOVIE_USER_COMMENT', data, res => {
        this.loading = 0
        cb && cb(res)
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
      })
    }
  },
  mounted () {
    this.loading = 1
    this.getList(res => {
      this.list = res.data.data
    })
  }
}
</script>
<style lang="scss" scoped>
#movieComment {
  height: 100vh;
  overflow: auto;
  padding-bottom: 100px;
  box-sizing: border-box;
  .no-list {
    padding: 143px 0;
    .iconSize {
      width: 104px;
      height: 130px;
      margin: 0 auto 30px;
      display: block;
    }

  }
  .list {
    padding-top: 30px;
  }
  .list-no-data {
    text-align: center;
    line-height: 45px;
    color: #666;
    font-size: 30px;
  }
  .comment-item {
    padding: 0 20px;
    border-bottom: 1px solid #EBEAEF;
    margin-bottom: 30px;
    .comment-type {
      font-weight: 900;
      font-size: 32px;
      line-height: 45px;
    }
    .content {
      padding: 7px 0 20px;
      line-height: 40px;
      font-size: 28px;
      color: #666;
    }
    .comment-grade {
      padding: 13px 0 6px;
    }
    .list-item {
      display: flex;
      flex-flow: row nowrap;
      box-sizing: border-box;
      padding: 30px 20px;
      box-shadow: 4px 4px 14px 0 #DADADA, -4px -4px 14px 0 #DADADA;
      width: 710px;
      margin: 0 auto;
      border-radius: 20px;
      .left-cover {
        width: 189px;
        height: 264px;
        margin-right: 26px;
        img {
          width: 100%;
        }
      }
      .rightContent {
        font-weight: 100%;
        position: relative;
        .title {
          line-height: 45px;
          max-height: 90px;
          height: 90px;
          overflow: hidden;
          text-overflow: -o-ellipsis-lastline;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          /* autoprefixer: off */
          -webkit-box-orient: vertical;
          /* autoprefixer: on */
        }
        .tip {
          font-size: 28px;
          line-height: 38px;
          margin-bottom: 10px;
          color: #666;
        }
        .grades {
          position: absolute;
          left: 0;
          bottom: 0px;
          white-space: nowrap;
          display: flex;
          .juejinbao {
            color: #F4A82B;
            margin-right: 40px;
          }
          .douban {
            color: #0db918
          }
        }
      }
    }
    .update-time {
      padding:  30px;
      color: #999;
      font-size: 24px;
      line-height: 34px;
      .digg {
        line-height: 1;
        display: flex;
        white-space: nowrap;
        align-items: flex-end;
        &.active {
          color: #ec3c11;
        }
        .digg-icon {
          width: 32px;
          height: 30px;
          margin-right: 8px;
          img {
            width: 100%;
          }
        }
      }
    }
  }
}
</style>



